/*custom dropdown st*/

.wrapper-dropdown-3 {
	/* Size and position */
	position: relative;
	min-width: 75px;
	margin: 0 auto;
	padding: 8px;
	padding-right: 40px;
	/* Styles */
	background: #fff;
	border-radius: 5px;
	border: 1px solid rgba(0, 0, 0, 0.15);
	box-shadow: 0 1px 1px rgba(50, 50, 50, 0.1);
	cursor: pointer;
	outline: none;
	/* Font settings */
	font-weight: bold;
	font-size: 13px;
	font-style: normal;
	font-stretch: normal;
	letter-spacing: -0.2px;
	color: #515151;
	z-index: 1000;
}
.wrapper-dropdown-3:after {
	content: "";
	width: 10px;
	height: 10px;
	position: absolute;
	right: 15px;
	top: 50%;
	margin-top: -3px;
	border-width: 6px 6px 0 6px;
	border-style: solid;
	border-color: #515151 transparent;
}
.wrapper-dropdown-3 .dropdown {
	/* Size & position */
	position: absolute;
	top: 140%;
	left: 0;
	right: 0;
	/* Styles */
	background: white;
	border-radius: inherit;
	border: 1px solid rgba(0, 0, 0, 0.17);
	box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
	font-weight: normal;
	transition: all 0.15s ease-in;
	list-style: none;
	padding: 0;
	/* Hiding */
	opacity: 0;
	pointer-events: none;
	text-align: left;
}
.wrapper-dropdown-3 .dropdown li a {
	display: block;
	padding: 8px 15px;
	text-decoration: none;
	color: #515151;
	border-bottom: 1px solid #e6e8ea;
	box-shadow: inset 0 1px 0 rgba(255, 255, 255, 1);
	transition: all 0.15s ease-out;
	font-weight: bold;
	font-size: 13px;
	font-style: normal;
	font-stretch: normal;
	letter-spacing: -0.2px;
}
.wrapper-dropdown-3 .dropdown li i {
	float: right;
	color: inherit;
}
.wrapper-dropdown-3 .dropdown li:first-of-type a {
	border-radius: 5px 5px 0 0;
}
.wrapper-dropdown-3 .dropdown li:last-of-type a {
	border-radius: 0 0 5px 5spx;
	border: none;
}
/* Hover state */

.wrapper-dropdown-3 .dropdown li:hover a {
	background: #ececec;
}
.wrapper-dropdown-3 .dropdown:after {
	content: "";
	width: 0;
	height: 0;
	position: absolute;
	bottom: 100%;
	right: 15px;
	border-width: 0 6px 6px 6px;
	border-style: solid;
	border-color: #fff transparent;
}
.wrapper-dropdown-3 .dropdown:before {
	content: "";
	width: 0;
	height: 0;
	position: absolute;
	bottom: 100%;
	right: 13px;
	border-width: 0 8px 8px 8px;
	border-style: solid;
	border-color: rgba(0, 0, 0, 0.1) transparent;
}
.wrapper-dropdown-3.active .dropdown {
	opacity: 1;
	pointer-events: auto;
}
